"use client";

import { useState, useMemo } from 'react';
import { motion } from 'framer-motion';
import {
  ArrowUpDown,
  MoreHorizontal,
  ExternalLink
} from 'lucide-react';
import { format } from 'date-fns';
import { Project } from '@/types';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { cn } from '@/lib/utils';

interface ProjectTableProps {
  projects: Project[];
  onProjectClick?: (project: Project) => void;
}

type SortField = 'name' | 'clientName' | 'totalBudget' | 'signDate' | 'status';
type SortOrder = 'asc' | 'desc';

// Updated Skeleton: Removed the header skeleton to match the new UI
export function ProjectTableSkeleton() {
  return (
    <div className="glass-card rounded-xl overflow-hidden border border-border/50">
      <div className="overflow-x-auto">
        <table className="w-full">
          <thead>
            <tr className="border-b border-border bg-muted/30">
              {[...Array(7)].map((_, i) => (
                <th key={i} className="p-4 text-left"><Skeleton className="h-4 w-20" /></th>
              ))}
            </tr>
          </thead>
          <tbody>
            {[...Array(5)].map((_, i) => (
              <tr key={i} className="border-b border-border/50">
                <td className="p-4"><Skeleton className="h-10 w-40" /></td>
                <td className="p-4"><Skeleton className="h-4 w-24" /></td>
                <td className="p-4"><Skeleton className="h-4 w-24" /></td>
                <td className="p-4"><Skeleton className="h-10 w-32" /></td>
                <td className="p-4"><Skeleton className="h-6 w-24" /></td>
                <td className="p-4"><Skeleton className="h-6 w-16" /></td>
                <td className="p-4"><Skeleton className="h-8 w-8 rounded-full" /></td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export function ProjectTable({ projects, onProjectClick }: ProjectTableProps) {
  // Removed local search and status filter states
  const [sortField, setSortField] = useState<SortField>('signDate');
  const [sortOrder, setSortOrder] = useState<SortOrder>('desc');

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-BD', {
      style: 'currency',
      currency: 'BDT',
      maximumFractionDigits: 0,
    }).format(amount);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'ONGOING':
        return 'bg-primary/20 text-primary border-primary/30';
      case 'COMPLETED':
        return 'bg-success/20 text-success border-success/30';
      case 'FUTURE':
        return 'bg-warning/20 text-warning border-warning/30';
      default:
        return 'bg-muted text-muted-foreground';
    }
  };

  // Logic now only handles sorting the pre-filtered results from the prop
  const sortedProjects = useMemo(() => {
    let result = [...projects];

    result.sort((a, b) => {
      let comparison = 0;
      switch (sortField) {
        case 'name':
          comparison = (a.name || "").localeCompare(b.name || "");
          break;
        case 'clientName':
          comparison = (a.clientName || "").localeCompare(b.clientName || "");
          break;
        case 'totalBudget':
          comparison = (a.totalBudget || 0) - (b.totalBudget || 0);
          break;
        case 'signDate':
          comparison = new Date(a.signDate).getTime() - new Date(b.signDate).getTime();
          break;
        case 'status':
          comparison = (a.status || "").localeCompare(b.status || "");
          break;
      }
      return sortOrder === 'asc' ? comparison : -comparison;
    });

    return result;
  }, [projects, sortField, sortOrder]);

  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortOrder('asc');
    }
  };

  const calculateProgress = (project: Project) => {
    if (!project.totalBudget || project.totalBudget === 0) return 0;
    const received = (project.bills || [])
      .filter(b => b.status === 'PAID')
      .reduce((sum, b) => sum + (Number(b.receivedAmount) || 0), 0);
    return Math.min(100, Math.round((received / project.totalBudget) * 100));
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.4 }}
      className="glass-card rounded-xl overflow-hidden border border-border/50"
    >
      <div className="overflow-x-auto">
        <table className="w-full">
          <thead>
            <tr className="border-b border-border bg-muted/30">
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">
                <button onClick={() => handleSort('name')} className="flex items-center gap-1 hover:text-foreground transition-colors">
                  Project <ArrowUpDown className="w-3 h-3" />
                </button>
              </th>
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">
                <button onClick={() => handleSort('clientName')} className="flex items-center gap-1 hover:text-foreground transition-colors">
                  Client <ArrowUpDown className="w-3 h-3" />
                </button>
              </th>
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">
                <button onClick={() => handleSort('signDate')} className="flex items-center gap-1 hover:text-foreground transition-colors">
                  Sign Date <ArrowUpDown className="w-3 h-3" />
                </button>
              </th>
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">
                <button onClick={() => handleSort('totalBudget')} className="flex items-center gap-1 hover:text-foreground transition-colors">
                  Budget <ArrowUpDown className="w-3 h-3" />
                </button>
              </th>
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">Progress</th>
              <th className="text-left p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">
                <button onClick={() => handleSort('status')} className="flex items-center gap-1 hover:text-foreground transition-colors">
                  Status <ArrowUpDown className="w-3 h-3" />
                </button>
              </th>
              <th className="text-right p-4 font-medium text-muted-foreground text-xs uppercase tracking-wider">Actions</th>
            </tr>
          </thead>
          <tbody>
            {sortedProjects.map((project, index) => {
              const progress = calculateProgress(project);
              const received = (project.bills || [])
                .filter(b => b.status === 'PAID')
                .reduce((sum, b) => sum + (Number(b.receivedAmount) || 0), 0);

              return (
                <tr
                  key={project.id}
                  className="border-b border-border/50 hover:bg-muted/10 transition-colors cursor-pointer group"
                  onClick={() => onProjectClick?.(project)}
                >
                  <td className="p-4">
                    <p className="font-semibold text-foreground text-sm group-hover:text-primary transition-colors">{project.name}</p>
                  </td>
                  <td className="p-4 text-xs text-muted-foreground">{project.clientName}</td>
                  <td className="p-4 text-xs text-muted-foreground">
                    {project.signDate ? format(new Date(project.signDate), 'MMM dd, yyyy') : 'N/A'}
                  </td>
                  <td className="p-4">
                    <p className="font-medium text-foreground text-sm">{formatCurrency(project.totalBudget)}</p>
                    <p className="text-[10px] text-muted-foreground">Rec: {formatCurrency(received)}</p>
                  </td>
                  <td className="p-4">
                    <div className="w-24">
                      <div className="flex items-center justify-between mb-1">
                        <span className="text-[10px] font-medium text-foreground">{progress}%</span>
                      </div>
                      <div className="h-1 bg-muted rounded-full overflow-hidden">
                        <div
                          className="h-full bg-primary rounded-full transition-all duration-500"
                          style={{ width: `${progress}%` }}
                        />
                      </div>
                    </div>
                  </td>
                  <td className="p-4">
                    <Badge variant="outline" className={cn("text-[10px] px-1.5 py-0 font-medium", getStatusColor(project.status))}>
                      {project.status}
                    </Badge>
                  </td>
                  <td className="p-4 text-right" onClick={(e) => e.stopPropagation()}>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="icon" className="h-8 w-8 opacity-50 hover:opacity-100"><MoreHorizontal className="w-4 h-4" /></Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end" className="w-40">
                        <DropdownMenuItem onClick={() => onProjectClick?.(project)}>
                          <ExternalLink className="w-3.5 h-3.5 mr-2" /> View Details
                        </DropdownMenuItem>
                        <DropdownMenuItem>Edit Project</DropdownMenuItem>
                        <DropdownMenuItem>Add Bill</DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>

      {sortedProjects.length === 0 && (
        <div className="p-12 text-center border-t border-border/50">
          <p className="text-sm text-muted-foreground">No projects found. Try adjusting your filters.</p>
        </div>
      )}
    </motion.div>
  );
}